.statistics {
 width: 60vw;
 display: flex;
 justify-content: space-between;
 align-items: center;
 flex-direction: column;
 border-radius: 5px;
 box-shadow:
  0 0 0 2px white,
  2px 2px 3rem rgba(81, 60, 60, 0.6);
 background-color: rgb(238, 238, 238);
}

.stat-list {
 display: flex;
 justify-content: space-evenly;
 list-style: none;
 width: 100%;
 font-size: larger;
 font-weight: 600;
 padding: 0;
 margin: 0;
}

.stat-list .item {
 width: calc(100% / 5);
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: space-evenly;
 padding-block: 20px;
 gap: 10px;
}


.item .label {
 opacity: 0.9;
}